import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, MenuController} from 'ionic-angular';
import {Storage } from "@ionic/storage";

@IonicPage()
@Component({
  selector: 'page-about',
  templateUrl: 'about.html',
})
export class AboutPage {

  useGame: boolean = false;

  constructor(public menuCtrl: MenuController, public navCtrl: NavController, public navParams: NavParams, private storage: Storage) {
    this.storage.get('useGame').then(data => this.useGame = data);
  }

  openMenu(){
    this.menuCtrl.open();
  }


}



